body {
	margin : 0px;
	padding : 0px;
	height : 100%;
	font-family: 'Chilanka', cursive;
	background-color : black;
	box-sizing: border-box;
}

.wrapper {
	margin : 0 auto;
	padding-top : 50px;
	padding-bottom : 50px;
	width : 550px;
	text-align : center;
}

h1 {
	color : magenta;
}

h1 span {
	color : white;
}

.ToDo_input {
	font-family: 'Chilanka', cursive;
	font-size : 24px;
	margin: 0 5px 5px 0;
	width: 85%;
	height : 42px;
	text-align : center;
	border : 2px solid cyan;
	border-radius : 20px;
	outline : none;
	color : black;
}

.add {
	width : 12%;
	height : 48px;
	border : 2px solid cyan;
	border-radius : 20px;
	color : magenta;
	outline : none;
	cursor : pointer;
}

.task {
	width: 85%;
	margin-top : 5px;
	padding-top : 10px;
	font-size : 24px;
	height : 36px;
	border : 2px solid lime;
	border-radius : 20px;
	float :left;
	color : white;
}

.del {
	width : 12%;
	height : 48px;
	margin-top : 7px;
	border : 2px solid lime;
	border-radius : 20px;
	color : red;
	outline : none;
	cursor : pointer;
}

.task input {
	float : left;
	margin: 7px 0 0 30px;
}